<template>
  <div>
    <el-table :data="items">
      <el-table-column label="名称" prop="name" sortable></el-table-column>
      <el-table-column label="路径" prop="path" sortable></el-table-column>
      <el-table-column label="状态" prop="state" sortable></el-table-column>

      <el-table-column label="操作">
        <template #default="scope">
          <el-button
            type="primary"
            link
            @click="handleClickImportItem(scope.row)"
            :disabled="disabledItemImport(scope.row)"
          >
            导入
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
function nop() {}

export default {
  name: "import-repo-state-table",

  computed: {
    items() {
      return this.$store.getters["import_repository/list"];
    },
  },

  methods: {
    handleSaveItemOK(item) {
      nop(item);
    },

    handleClickImportItem(item) {
      let items = [item];
      this.$store.dispatch("import_repository/save", { items }).then(() => {
        this.handleSaveItemOK(item);
      });
    },

    disabledItemImport(item) {
      if (item == null) {
        return true;
      }
      let state = item.state;
      return state == "ready";
    },
  },
};
</script>
