<template>
  <div>
    <h1>导入单个本地仓库</h1>

    <div class="search-path-bar">
      <el-input v-model="path" placeholder="输入仓库的本地路径">
        <template #prepend> 仓库路径 </template>

        <template #append>
          <el-button @click="handleClickBrowse">浏览 ...</el-button>
        </template>
      </el-input>

      <el-button class="btn-search" type="primary" @click="handleClickFind"
        >查找</el-button
      >
    </div>

    <MyStateTable class="state-table" />
    <MyOpenFileDialog
      v-model="displayOpenDialog"
      @open-file="handleOpenFile"
      want="folder"
      action="open"
    />
  </div>
</template>

<script>
import MyStateTable from "./../import-local-repo/import-repo-state-table.vue";
import MyOpenFileDialog from "./../filebrowser/dialog-open.vue";

export default {
  name: "import-local-repository",

  components: { MyStateTable, MyOpenFileDialog },

  data() {
    return {
      path: "",
      displayOpenDialog: false,
    };
  },

  methods: {
    handleClickFind() {
      let path = this.path;
      this.$store.dispatch("import_repository/locate", { path });
    },

    handleClickBrowse() {
      this.displayOpenDialog = true;
    },

    handleOpenFile(files) {
      if (files == null) {
        return;
      }
      for (var i in files) {
        let f = files[i];
        this.path = f.path;
        return;
      }
    },
  },

  mounted() {
    this.$store.commit("import_repository/reset");
  },
};
</script>

<style scoped>
.state-table {
  margin-top: 30px;
}

.search-path-bar {
  display: flex;
  flex-direction: row;
}

.btn-search {
  margin-left: 10px;
}
</style>
